<template>
	<div class="model-page">
		<div id="container"></div>
        <video class="videoBox" preload id="video" muted loop crossOrigin="anonymous" playsinline>
            <source type='video/mp4' :src="modelUrl" >
        </video>
        <div class="playBtn" v-if="!playStatus">
            <span @click="playVideo" v-if="!playStatus">播放</span>
            <span v-else>暂停</span>
        </div>
	</div>
</template>
<script>
import * as Three from 'three'
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls";
export default {
    name:'Vrvideo',
	props: {
		modelUrl: {
			type: String,
			default: 'https://wmpa.gansumuseum.com/sccp-file/ganbovr/origin/huozhongpian.mp4',
			des:'模型地址'
		},
	},
	data() {
		return {
			renderer: '', //渲染器
			scene: '', //场景
			light: '', //光源
			camera: '', //相机
			controls: '', //控制器
			mesh:null,
            playStatus:false,
            phi : 0, 
            theta : 0,
            beta : 0            //计算相机位置的参数    
		}
	},
	mounted(){
		this.init()
		this.animate()
        window.addEventListener('resize',this.onWindowResize())
        this.getGyro()
	},
	methods: {
		animate(){
			requestAnimationFrame(this.animate)
			this.renderer.render(this.scene, this.camera); //执行渲染操作
            this.controls.update()
		},
        //监听横竖屏重新设置尺寸
        onWindowResize() {
            this.camera.aspect = window.innerWidth / window.innerHeight;
            this.camera.updateProjectionMatrix();
            this.renderer.setSize(window.innerWidth, window.innerHeight);
        },
        playVideo(){
            var video = document.getElementById('video')
            if(this.playStatus){
                this.playStatus = false
                video.pause();
            }else{
                this.playStatus = true
                video.play();
            }
        },
        // play(){
        //     var video = document.getElementById('video')
        //     video.play();
        //     this.playStatus = true
        // },
        // pause(){
        //     var video = document.getElementById('video')
        //     video.pause();
        //     this.playStatus = false
        // },

        getGyro(){
            let that = this;
            if (window.DeviceOrientationEvent) {
                console.log("支持 deviceorientation", window.DeviceOrientationEvent)
                window.addEventListener('deviceorientation', function(e){
                    // 以设备坐标系z轴为轴，旋转alpha度。alpha的作用域为(0, 360)
                    // 以设备坐标系x轴为轴，旋转beta度。beta的作用域为(-180, 180)
                    // 已设备坐标系y轴为轴，旋转gamma度。gamma的作用域为(-90, 90)
                    console.log(e);
                    that.alpha= parseInt(e.alpha);
                    that.beta= parseInt(e.beta);
                    that.gamma= parseInt(e.gamma);
                    console.log('alpha', that.alpha)
                    console.log('beta', that.beta)
                    console.log('gamma', that.gamma)    
                });

                // phi = THREE.Math.degToRad(90 - lat);
                // theta = THREE.Math.degToRad(lon);
                // camera.target.x = 500 * Math.sin(phi) * Math.cos(theta);//X轴的坐标
                // camera.target.y = -500 * Math.cos(phi);//y轴的坐标
                // camera.target.z = 500 * Math.sin(phi) * Math.sin(theta);//z轴的坐标
                // camera.lookAt(camera.target);
            } else {
                console.log("--不--支持 deviceorientation")
            }
        },

		init(){
            let container = document.getElementById('container');
            this.camera = new Three.PerspectiveCamera(45, container.clientWidth / container.clientHeight, 0.1, 1000);
            this.camera.position.set(80, 30, 30)

            this.scene = new Three.Scene();
            this.scene.background = new Three.Color("#ccc")
            this.scene.environment = new Three.Color("#ccc")
            let sphereGeometry = new Three.SphereGeometry(200, 60, 60);
            sphereGeometry.scale(-1, 1, 1);

            //添加材质     
            var video = document.getElementById('video')
            // video.play();
            var texture = new Three.VideoTexture(video);

            texture.magFilter = Three.LinearFilter;//设置贴纸素材的质量
            texture.minFilter = Three.LinearFilter;

            texture.colorSpace = Three.SRGBColorSpace;
            const material = new Three.MeshBasicMaterial(
                {
                    // color: 0x00ff00,
                    map: texture,
                    //  双面渲染  
                    side: Three.DoubleSide,
                    metalness:0.25,  // 金属度  ，0代表非金属
                    roughness:0,     // 粗糙度  越小越光滑

                });

            this.mesh = new Three.Mesh(sphereGeometry, material);
            this.scene.add(this.mesh);
            this.scene.rotation.set(0, 0, 0); 

            this.renderer = new Three.WebGLRenderer({
                antialias: true,
                precision: 'highp', 
                mipmap: 'highp',            //   加上precision 和 mipmap参数，调整画面清晰度
            });

            //指定渲染器的高宽(和画布框的大小一致)
            this.renderer.setPixelRatio(window.devicePixelRatio);
            this.renderer.setSize(container.clientWidth, container.clientHeight);
            //将指定的渲染器加入到Dom容器中
            container.appendChild(this.renderer.domElement);

            this.controls = new OrbitControls(this.camera, this.renderer.domElement); // 初始化控制器

            this.controls.minDistance = 1; // 设置移动的最短距离（默认为零）
            this.controls.maxDistance = 100; // 设置移动的最长距离（默认为无穷）
            this.controls.maxPolarAngle = 100 / 360 *2 * Math.PI; //绕垂直轨道的距离（范围是0-Math.PI,默认为Math.PI） 
            this.controls.minPolarAngle = 0; 
            this.controls.enablePan = false; //禁用右键功能
            this.controls.enableDamping = true    //阻尼   渲染函数调用update 重新计算
		},



	}
}
</script>

<style lang="less">
*{
    margin: 0;
    padding: 0;
}
.model-page{
    width: 100vw;
    height: 100vh;
    overflow: hidden;
}
#container {
    width: 100%;
    height: 100%;
    overflow: hidden;
    // height: 1080px;
}
.playBtn{
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, .6);
    // display: flex;
    // align-content: center;
    // justify-content: center;
   
    span{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50% -50%);
        width: 50px;
        height: 10px;
        padding: 10px;
        background-color: #fff;
    }
}
.videoBox{
    display: none;

}
</style>